@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@300;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mainPadding{margin:0 1rem 1rem}.rounded-box{margin:1rem;padding:1rem;border:1px solid rgba(0,0,0,0.09) !important;border-radius:5px}.rounded-box.primary{border-width:1.5px !important;border-color:#dcdcdc !important}h1,li{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}h1,p{text-align:left}p{margin:1.5rem}ul{list-style:none;padding:.5rem;margin:0}li{display:flex;align-items:center;cursor:pointer}li svg{color:#5346d4d4;margin-right:1rem}li:hover{background-color:rgba(64,59,86,0.07)}

.mainPadding{margin:0 1rem 1rem}.rounded-box{margin:1rem;padding:1rem;border:1px solid rgba(0,0,0,0.09) !important;border-radius:5px}.rounded-box.primary{border-width:1.5px !important;border-color:#dcdcdc !important}h2{text-align:center}.newTaskInput{display:flex}.newTaskInput input{outline-style:none;font-size:1.2rem;border:1px solid;margin-right:0;border-top-right-radius:0;border-bottom-right-radius:0;flex:1 1}.newTaskInput svg{cursor:pointer;font-size:1.8rem;border-left:none !important;color:#7d74de;margin-left:0;border-top-left-radius:0;border-bottom-left-radius:0}

.mainPadding{margin:0 1rem 1rem}.rounded-box{margin:1rem;padding:1rem;border:1px solid rgba(0,0,0,0.09) !important;border-radius:5px}.rounded-box.primary{border-width:1.5px !important;border-color:#dcdcdc !important}*{font-family:'Montserrat Alternates', sans-serif}p,h1,span,h2{color:#373737}.App-logo{height:10vh;pointer-events:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.App-header{background-color:#282c34;min-height:2vh;display:flex;flex-direction:row;align-items:center;font-size:calc(10px + 2vmin)}.App-header p{color:white;margin:0}h2{margin:0;padding:1rem 2rem 0}.TodoList{padding:1rem}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

