@import '../styles/styles';

h2 {
  text-align: center;
}

.newTaskInput {
  display: flex;

  input {
    outline-style: none;
    font-size: 1.2rem;
    border: 1px solid;
    margin-right: 0;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    flex: 1;
  }

  svg {
    cursor: pointer;
    font-size: 1.8rem;
    border-left: none !important;
    color: #7d74de;
    margin-left: 0;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
}
